#MobilePhone {
  width: 25rem;
  height: 45rem;
  border: 4px solid black;
  width: 24.3rem;
  border-radius: 29px;
  background-color: #03164c;
}
.MobileContainer {
  border: 5px solid black;
  border-radius: 18px;
  width: 240px;
  height: 440px;
  background-color: #03164c;
  background-image: url('../../images/Vector.svg'),
    url('../../images/bcImg.svg');
  background-repeat: no-repeat, no-repeat;
  background-position: left 0% bottom 0%, right 0% bottom 0%;
}
.landscapeImage {
  background-image: url('../../images/Landscape.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 18px;
  height: 130px;
  -webkit-mask-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(rgba(0, 0, 0, 1)),
    to(rgba(0, 0, 0, 0))
  );
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

#Landscape {
  width: 23.5rem;
  position: relative;
  left: -15px;
  top: 0px;
  border-radius: 30px;
}
#buttonControls {
  width: 2rem;
  margin: 1rem;
}

#Mobile-phone-text {
  color: #fff;
  line-height: 3.4rem;
  font-size: 2rem;
  font-family: monospace;
}
#text-container {
  padding: 7%;
  background-image: url('../../images/lineargradient.png');
  background-color: #03164c;
  text-align: center;
  font-size: 1.4rem;
}
.icons {
  text-align: center;
  padding: 1rem;
}
#span-text {
  font-family: 'Italianno', cursive;
}
#play-button {
  background-color: transparent;
  border: none !important;
}
.background {
  background-color: #03164c;
  text-align: center;
}
#duration {
  text-align: center;
  padding: 1rem;
  margin-top: 1rem;
  height: 4.5rem;
  background-image: url(../../images/Vector.svg), url('../../images/bcImg.svg');
  background-position: bottom left, bottom right;
  background-repeat: no-repeat, no-repeat;
  border: 1px solid #03164c;
}
input[type='range'] {
  -webkit-appearance: none;
  width: 80%;
  outline: none;
  height: 2px;
  margin-top: 2rem;
}
input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  background: #7f6ecc;
  border-radius: 50%;
  cursor: pointer;
}
.right input[type='range'] {
  width: 40%;
}
