#text {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 3rem;
  line-height: 3.6rem;
  letter-spacing: 1px;
  color: #ffffff;
  text-transform: uppercase;
  margin-left: 1rem;
}
.audioPlayerMobile {
  background-color: #937de2;
}
#fmobile {
  display: none;
}
#avail {
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 1.4rem;
  letter-spacing: 1.5px;
  color: #f8e71c;
}
#avails {
  font-family: 'Lato', sans-serif;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 1.4rem;
  letter-spacing: 1.5px;
  color: #f8e71c;
}
#mobile {
  height: 49rem;
  position: relative;
  width: 26rem;
  left: -35px;
  bottom: 31px;
}
#span {
  font-family: Merriweather;
  font-weight: 700;
  font-style: normal;
  size: 3rem;
  line-height: 3.6rem;
  color: #f8e71c;
}
#play {
  margin-top: -2%;
  height: 4.7rem;
  width: 15.2rem;
}

#body {
  position: relative;
  right: -36px;
  top: -406px;
  height: 437px;
  width: 248px;
}

@media screen and (max-width: 1000px) {
  html {
    font-size: 45%;
  }
  #text {
    text-align: center;
  }
  #fmobile {
    display: inline;
    text-align: center;
  }
  #PlayButton {
    background-color: #937de2;
    border: none;
    border-radius: 18rem;
  }
  #audioPlayterMobile {
    margin-top: 5rem;
    margin-bottom: 5rem;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    border: 1px solid #937de2;
    border-radius: 18px;
    background-color: #937de2;
  }
  .audioText {
    text-overflow: ellipsis;
    color: white;
    padding: 5px;
  }
  #mobile {
    display: none;
  }
  #avial {
    letter-spacing: 1px;
  }
  #avails {
    letter-spacing: 1px;
  }

  #body {
    display: none;
  }
  #playstoreButton {
    justify-content: flex-end;
  }
  #btnwidth {
    width: 44%;
  }
  #Mobilephone {
    display: none;
  }
}
